<script>
import $ from "jquery";
export default {
  data: () => ({
    imgHeight: 700,
    smallWidth: false
  }),
  computed:{
    mainItems(){
      return this.$store.getters.getMain;
    }
  },
  created(){
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  mounted(){
    this.$store.commit('changeTitle','main project');
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', false);
    }
  },
  methods:{
    handleResize(){
      var width = window.innerWidth;
      // width = 1264 이하 일때 동작하지 않음
      if( width > 1264 ){
        this.smallWidth = true;
        this.imgHeight = 700;
      }if( width < 768 ){
        this.imgHeight = 600;
      }if( width < 600 ){
        this.smallWidth = false;
        this.imgHeight = 500;
      }
    },
    link(value){
      this.$store.dispatch('changeMain',value);
      this.$router.push('/mainContents').catch(err => err);
    }
  },
  updated(){
  },
  watch:{
  },
  beforeDestroy(){
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', true);
    }
    window.removeEventListener('resize', this.handleResize)
  }
}
</script>

